import React from 'react';
// import PropTypes from 'prop-types';
// import { directive } from '@babel/types';
import $ from 'jquery';
import Fingerprint2 from './components/fingerprint2';
import Table from './components/table';

// function App({ name }) {
//   return <h1>Hello {name}</h1>;
// }
// App.propTypes = {
//   name: PropTypes.string.isRequired,
// };

let fingerArr = [];
let geoDataArr = [];
function table(finger) {
  const keys = Object.keys(finger);
  keys.forEach(el => {
    fingerArr.push(finger[el].value.toString());
  });
  fingerArr = fingerArr.map(value => {
    let val = value;
    if (value.length > 100) {
      val = value.substring(0, 100);
    }
    return val;
  });
}

if (window.requestIdleCallback) {
  requestIdleCallback(() => {
    Fingerprint2.get(components => {
      const finger = components;
      table(finger);
    });
  });
} else {
  setTimeout(() => {
    Fingerprint2.get(components => {
      const finger = components;
      table(finger);
    });
  }, 500);
}

$.getJSON('http://gd.geobytes.com/GetCityDetails?callback=?', data => {
  const geoData = JSON.parse(JSON.stringify(data, null, 2));

  const keys = Object.keys(geoData);
  keys.forEach(el => {
    geoDataArr.push(geoData[el].toString());
  });
  console.log(geoData.geobytesremoteip);
  console.log(geoDataArr);
});

class App extends React.Component {
  render() {
    return (
      <div>
        <Table fingerArr={fingerArr} geoDataArr={geoDataArr} />
      </div>
    );
  }
}

export default App;
