/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
import React from 'react';
// import PropTypes from 'prop-types';
// import { directive } from '@babel/types';

const Table = props => {
  return (
    <div>
      <h3 className="indent-up">
        Hi{props.name}. You're looking at the <strong> info your browser gave us</strong>.
      </h3>
      <table className="table-fill">
        <thead>
          <tr>
            <th className="text-left">Attribute</th>
            <th className="text-left">Value</th>
          </tr>
        </thead>
        <tbody className="table-hover">
          <tr>
            <td className="text-left">IP</td>
            <td className="text-left">{props.geoDataArr[1]}</td>
          </tr>
          <tr>
            <td className="text-left">Country</td>
            <td className="text-left">{props.geoDataArr[5]}</td>
          </tr>
          <tr>
            <td className="text-left">City</td>
            <td className="text-left">{props.geoDataArr[16]}</td>
          </tr>
          <tr>
            <td className="text-left">Coordinates</td>
            <td className="text-left">
              X: {props.geoDataArr[14]}, Y: {props.geoDataArr[15]}
            </td>
          </tr>
          <tr>
            <td className="text-left">User Agent</td>
            <td className="text-left">{props.fingerArr[0]}</td>
          </tr>
          <tr>
            <td className="text-left">Web Driver</td>
            <td className="text-left">{props.fingerArr[1]}</td>
          </tr>
          <tr>
            <td className="text-left">Language</td>
            <td className="text-left">{props.fingerArr[2]}</td>
          </tr>
          <tr>
            <td className="text-left">Color Depth</td>
            <td className="text-left">{props.fingerArr[3]}</td>
          </tr>
          <tr>
            <td className="text-left">Device Memory</td>
            <td className="text-left">{props.fingerArr[4]}</td>
          </tr>
          <tr>
            <td className="text-left">Hardware Concurrency</td>
            <td className="text-left">{props.fingerArr[5]}</td>
          </tr>
          <tr>
            <td className="text-left">Screen Resolution</td>
            <td className="text-left">{props.fingerArr[6]}</td>
          </tr>
          <tr>
            <td className="text-left">Available Screen Resolution</td>
            <td className="text-left">{props.fingerArr[7]}</td>
          </tr>
          <tr>
            <td className="text-left">Timezone Offset</td>
            <td className="text-left">{props.fingerArr[8]}</td>
          </tr>
          <tr>
            <td className="text-left">Timezone</td>
            <td className="text-left">{props.fingerArr[9]}</td>
          </tr>
          <tr>
            <td className="text-left">Session Storage</td>
            <td className="text-left">{props.fingerArr[10]}</td>
          </tr>
          <tr>
            <td className="text-left">Local Storage</td>
            <td className="text-left">{props.fingerArr[11]}</td>
          </tr>
          <tr>
            <td className="text-left">Indexed Db</td>
            <td className="text-left">{props.fingerArr[12]}</td>
          </tr>
          <tr>
            <td className="text-left">Add Behavior</td>
            <td className="text-left">{props.fingerArr[13]}</td>
          </tr>
          <tr>
            <td className="text-left">Open Database</td>
            <td className="text-left">{props.fingerArr[14]}</td>
          </tr>
          <tr>
            <td className="text-left">Cpu Class</td>
            <td className="text-left">{props.fingerArr[15]}</td>
          </tr>
          <tr>
            <td className="text-left">Platform</td>
            <td className="text-left">{props.fingerArr[16]}</td>
          </tr>
          <tr>
            <td className="text-left">Plugins</td>
            <td className="text-left">{props.fingerArr[17]}</td>
          </tr>
          <tr>
            <td className="text-left">Canvas</td>
            <td className="text-left">{props.fingerArr[18]}...</td>
          </tr>
          <tr>
            <td className="text-left">Webgl</td>
            <td className="text-left">{props.fingerArr[19]}...</td>
          </tr>
          <tr>
            <td className="text-left">Webgl Vendor And Renderer</td>
            <td className="text-left">{props.fingerArr[20]}</td>
          </tr>
          <tr>
            <td className="text-left">AdBlock</td>
            <td className="text-left">{props.fingerArr[21]}</td>
          </tr>
          <tr>
            <td className="text-left">Has Lied Languages</td>
            <td className="text-left">{props.fingerArr[22]}</td>
          </tr>
          <tr>
            <td className="text-left">Has Lied Resolution</td>
            <td className="text-left">{props.fingerArr[23]}</td>
          </tr>
          <tr>
            <td className="text-left">Has Lied OS</td>
            <td className="text-left">{props.fingerArr[24]}</td>
          </tr>
          <tr>
            <td className="text-left">Has Lied Browser</td>
            <td className="text-left">{props.fingerArr[25]}</td>
          </tr>
          <tr>
            <td className="text-left">Touch Support</td>
            <td className="text-left">{props.fingerArr[26]}</td>
          </tr>
          <tr>
            <td className="text-left">Fonts</td>
            <td className="text-left">{props.fingerArr[27]}</td>
          </tr>
          <tr>
            <td className="text-left">Audio</td>
            <td className="text-left">{props.fingerArr[28]}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Table;
